<template>
	<div class="container">
		<div class="control-form">
			<ul class="upload-imgs">
				<li v-for='(value, key) in imgs' :key="key">
					<img class="img" :src="getObjectURL(value)"><a class="close" @click="delImg(key)">×</a>
				</li>
				<li v-if="imgLen>=max ? false : true">
					<input type="file" class="upload" @change="addImg" ref="inputer" multiple accept="image/png,image/jpeg,image/jpg" />
					<a class="add">
						<i class="iconfont">+</i>
						<p>点击上传</p>
					</a>
				</li>
			</ul>
		</div>
		<button @click="submit">提交</button>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'test_demo',
		data() {
			return {
				formData: new FormData(),
				imgs: [],
				imgLen: 0,
				max: 1
			}
		},
		methods: {
			addImg(event) {
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				this.fil = inputDOM.files;
				let oldLen = this.imgLen;
				let len = this.fil.length + oldLen;
				if (len > this.max) {
					alert('最多可上传4张，您还可以上传' + (4 - oldLen) + '张');
					return false;
				}
				console.log(this.fil)
				for (let i = 0; i < this.fil.length; i++) {
					let size = Math.floor(this.fil[i].size);
					/* if (size > 2 * 1024 * 1024) {
						alert('请选择2M以内的图片！');
						return false
					} */
					this.imgLen++;
					this.imgs.push(this.fil[i])
				}
			},
			getObjectURL(file) {
				var url = null;
				if (window.createObjectURL != undefined) { // basic
					url = window.createObjectURL(file);
				} else if (window.URL != undefined) { // mozilla(firefox)
					url = window.URL.createObjectURL(file);
				} else if (window.webkitURL != undefined) { // webkit or chrome
					url = window.webkitURL.createObjectURL(file);
				}
				return url;
			},
			delImg(key) {
				this.$delete(this.imgs, key);
				this.imgLen--;
			},
			submit() {
				this.imgs.forEach(item => {
					this.formData.append('file', item, new Date().getTime() + '.' + item.type.split('/')[1])
				})
				axios({
					method: 'post',
					url: this.$baseUrl() + '/users/upload/visit',
					data: this.formData,
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				}).then( res => {
					console.log(res.data.data)
				}).catch((err) => {
					this.$toast.failed("图片上传失败")
				})
			}

		}
	}
</script>

<style scoped="scoped">
	.container {
		padding: 10px;
		box-sizing: border-box;
	}

	.upload-imgs {
		display: flex;
		flex-wrap: wrap;
	}

	.upload-imgs li {
		position: relative;
		width: 100px;
		height: 100px;
		font-size: 14px;
		margin-right: 10px;
		margin-bottom: 10px;
		border: 1px solid #ccc;
		overflow: hidden;
	}

	.upload-imgs .add {
		display: block;
		position: relative;
		z-index: 0;
		background-color: #ccc;
		color: #ffffff;
		height: 94px;
		padding: 8px 0;
	}

	.upload-imgs .add .iconfont {
		display: block;
		text-align: center;
		font-size: 40px;
	}
	.upload-imgs .add p {
		text-align: center;
	}

	.upload-imgs li .upload {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		width: 100px;
		height: 100px;
		opacity: 0;
		z-index: 1;
	}

	.upload-imgs .img {
		display: block;
		width: 100px;
		height: 100px;
	}

	.upload-imgs .close {
		position: absolute;
		right: 2px;
		top: 2px;
		color: #fff;
		background-color: #000;
		border-radius: 50%;
		width: 18px;
		height: 18px;
		line-height: 18px;
		text-align: center;
	}
</style>
